	import uniList from "../../components/uni-list/uni-list.vue";
	import uniListItem from "../../components/uni-list-item/uni-list-item.vue";
	import emptyContent from "../../components/common/empty-content.vue";
	export default {
		components: {
			uniList,
			uniListItem,
			emptyContent
		},
		data() {
			return {
				firstload:false,
				list:[]
			}
		},
		onLoad() {
			this.getList();
		},
		onNavigationBarButtonTap() {
            this.removeHistory()
        },
		methods: {
            removeHistory(){
                uni.showModal({
                    title: '提示',
                    content: '是否要清除浏览历史？',
                    success: res => {
                        if (res.confirm) {
                            this.User.clearHistory();
                            this.list = [];
                            uni.showToast({ title: '清除成功' });
                        }
                    }
                });
            },
			getList(){
				try{
					let list = uni.getStorageSync('HistoryList_'+this.User.userinfo.id);
					this.list = list ? JSON.parse(list) : [];
				}catch(e){
					uni.showToast({ title: '加载失败~', icon: 'none' });
				}
				this.firstload = true;
			},
			openDetail(item){
				uni.navigateTo({
					url: '/pages/detail/detail?detailData='+JSON.stringify(item),
				});
			}
		}
	}
